import { Component, OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { MonitorService } from '../../../service/monitor.service';
import { AirmonitorService } from '../../../service/airmonitor.service';
import { CircleOverlarAirService } from '../../../service/circle-overlay-air.service';

// baidu map
declare let BMap;

declare let BMAP_ANCHOR_TOP_LEFT;

@Component({
  selector: 'app-air',
  templateUrl: './air.component.html',
  styleUrls: ['./air.component.scss']
})
export class AirComponent implements OnInit, OnDestroy {

  @ViewChild('map6') map_container: ElementRef;
    /*
  model:object
  airdevicelist: array // 
  */
  model: any = {}; // 
  /*
  map_model: object // 
  @currentCity: any // 
  @currentArea: any // 
  @cityList: array // 
  @currentChildren: array // 
  @currentBlock: array //  = []; // 2
*/

  map_model: any = {}; // 

  map: any; // 

  currentAirIndex: any; // 

  visible = true; // 
  areashow = false; // 
  cityshow = false; // 

  parentNode = null; // JSON 
  node = null; // JSON 

  indexofHtml: any; // 
  allIndexs = [ // 
    {
      id: '',
      name: 'PM2.5'
    },
    {
      id: '',
      name: 'PM10'
    },
    {
      id: '',
      name: 'TVOC'
    },
    {
      id: '',
      name: ''
    },
    {
      id: '',
      name: ''
    },
  ];
  timer: any; // 


  constructor(private monitorService: MonitorService, private airmonitorService: AirmonitorService,
    public router: Router) {
    this.indexofHtml = this.allIndexs[0];
    this.currentAirIndex = 'PM2.5';
    this.model.airdevicelist = []; // 
    this.map_model.cityList = []; // 
    this.map_model.currentChildren = []; // 
    this.map_model.currentBlock = []; // //  = []; // 2
  }
  ngOnInit() {
    this.addBeiduMap();
    this.getCity(); // 
  }

  // API
  addBeiduMap() {

    const map = this.map = new BMap.Map(this.map_container.nativeElement, {
      enableMapClick: true,
      minZoom: 11,
      // maxZoom : 11
    }); // 

    // BMapPointPoint116.40439.915
    const point = new BMap.Point(113.950723, 22.558888); // 
    map.centerAndZoom(point, 15); // 
    // map.setMapStyle({ style: 'googlelite' });

    // 
    const offset = new BMap.Size(20, 55);
    const navigationControl = new BMap.NavigationControl({
      anchor: BMAP_ANCHOR_TOP_LEFT,
      offset: offset,
    });
    map.addControl(navigationControl);

    this.getAirdevices(); // 
    this.timer = setInterval(() => {
      this.getAirdevices(); // 
    }, 5000);

    map.enableScrollWheelZoom(true); // 
    // const marker = new BMap.Marker(point);  // 
    // map.addOverlay(marker);               // 
    this.dragendOff(map);
    this.zoomendOff(map);
  }

  // 

  // --
  dragendOff(baiduMap) {
    const that = this;
    baiduMap.addEventListener('dragend', function () {
      that.model.airdevicelist = [];
      baiduMap.clearOverlays();
      that.getAirdevices(); // -
    });
  }
  // --
  zoomendOff(baiduMap) {
    const that = this;
    baiduMap.addEventListener('zoomend', function () {
      // if (that.isqueryPoint === true) {
      //   that.isqueryPoint = false;
      // } else {
      that.model.airdevicelist = [];
        baiduMap.clearOverlays();
        that.getAirdevices(); // 
        // console.log('' + baiduMap.getZoom() + '');
      // }
    });
  }

  // 
  getAirdevices() {
    const that = this;
    const Bounds = this.map.getBounds(); // 
    const NorthEast = Bounds.getNorthEast(); // 
    const SouthWest = Bounds.getSouthWest(); // 
    localStorage.setItem('NE', JSON.stringify(NorthEast));
    localStorage.setItem('SW', JSON.stringify(SouthWest));

    let compar;
    let value;
    this.airmonitorService.getAirDevice(NorthEast, SouthWest).subscribe({
      next: function (val) {
        // value = val;
        const curIndex = that.currentAirIndex;
        compar = that.comparison(that.model.airdevicelist, val);
        // console.log(compar);
        value = that.judgeChange(compar.a_arr, compar.b_arr, curIndex);

        that.changeMarker(value); // 
        that.deleMarker(compar.a_surplus); // 
        // that.deleMarker(value); // 
        that.addCertainMarker(compar.b_surplus, curIndex); // 
        // that.addPoint(value); // 

        that.model.airdevicelist = val; // 
      },
      complete: function () {
        // that.addCertainMarker(value, that.currentAirIndex);
      },
      error: function (error) {
        console.log(error);
        console.log(NorthEast);
        console.log(SouthWest);
      }
    });
  }

  // 
  comparison(a, b) {
    const a_arr: any[] = [];
    const b_arr: any[] = [];
    const a_surplus: any[] = [];
    const b_surplus: any[] = [];
    let i = 0;
    for (let j = 0; j < b.length; j++) {
      while (i < a.length && a[i].id < b[j].id) {
        a_surplus.push(a[i]);
        i++;
      }
      if (i >= a.length || a[i].id > b[j].id) {
        b_surplus.push(b[j]);
      } else {
        a_arr.push(a[i]);
        i++;
        b_arr.push(b[j]);
      }
    }
    return {
      a_arr: a_arr,
      b_arr: b_arr,
      a_surplus: a_surplus,
      b_surplus: b_surplus,
    };
  }
  // 
  judgeChange(a, b, curIndex) {
    const changePoint: any[] = [];
    const length = a.length < b.length ? a.length : b.length;
    let value;
    switch (curIndex) {
      case 'PM2.5': value = 'pm25'; break;
      case 'PM10': value = 'pm10'; break;
      case 'TVOC': value = 'tvoc'; break;
      case '': value = 'temperature'; break;
      case '': value = 'humidity'; break;
      default: break;
    }

    for (let index = 0; index < length; index++) {
      const a_element = a[index];
      const b_element = b[index];
      if (a_element.error !== b_element.error ||
        a_element.offline !== b_element.offline ||
        a_element[value] !== b_element[value]
       ) {
        changePoint.push(b_element);
      }

    }
    return changePoint;

  }

  // 
  changeMarker(airdevice_list) {
    this.deleMarker(airdevice_list); // 
    this.addCertainMarker(airdevice_list, this.currentAirIndex); // 
  }
  // 
  deleMarker(airdevice_list) {
    const makers = this.map.getOverlays();
    for (let ind = 0; ind < airdevice_list.length; ind++) {
      const ele = airdevice_list[ind];
      const point = airdevice_list[ind].point;
      for (let index = 0; index < makers.length; index++) {
        const element = makers[index];
        const lat = element.point && element.point.lat;
        const lng = element.point && element.point.lng;
        if (point.lat === lat && point.lng === lng) {
          this.map.removeOverlay(makers[index]);
        }

      }
    }
  }

  // 
  addCertainMarker(val, index) {
    const markers = [];
    const points: any[] = [];

    const that = this;
    const length = 60;  // 
    let color;  // 
    color = '#4eb4cf'; // 
    const name = index; // 
    let indexvalue; // 
    const mouseoverColor = '#9bd9dd';  // 

    val.map((item, i) => {
      const point = new BMap.Point(item.point.lng, item.point.lat);
      let myIcon;
      switch (index) {
        case 'PM2.5': indexvalue = item.pm25; break;
        case 'PM10': indexvalue = item.pm10; break;
        case 'TVOC': indexvalue = item.tvoc; break;
        case '': indexvalue = item. temperature + ' C'; break;
        case '': indexvalue = item.humidity; break;
        default: break;
      }
      // 
      myIcon = new CircleOverlarAirService(point, name, indexvalue, length, color, mouseoverColor);
      this.map.addOverlay(myIcon);

      markers.push(myIcon); // 
      points.push(point);
    });
    // 
    for (let indexl = 0; indexl < markers.length; indexl++) {
      const marker = markers[indexl];
      const item = val[indexl];
      that.openSideBar(marker, that.map, item, points[indexl]);
    }
  }


  // -
  openSideBar(marker, baiduMap, airDevice, point) {
    // console.log(airDevice);
    const that = this;
    const opts = {
      width: 350,     // 
      // height: 100,     // 
      // title: `${val.name} | ${val.id }`, // 
      // enableMessage: true, // 
      enableAutoPan: true, // 
      // border-radius: 5px,
    };  // ${airDevice.id} ${airDevice.description}
    let txt = `<p style='font-size: 12px; line-height: 1.8em; border-bottom: 1px solid #ccc; padding-bottom: 10px;'>`;
    txt = txt + ` | ${airDevice.name} </p><p> ${airDevice.description}</p>`;
    if (airDevice.offline === false) {
      txt = txt + `<p> </p>`;
    } else {
      txt = txt + `<p> <span style='color: red'></span></p>`;
    }
    if (airDevice.error === false) {
      txt = txt + `<p> </p>`;
    } else {
      txt = txt + `<p> <span style='color: red'></span></p>`;
    }
    const infoWindow = new BMap.InfoWindow(txt, opts);
    marker.V.addEventListener('click', function () {
      baiduMap.openInfoWindow(infoWindow, point); // 
    });
  }

  //  --ok
  getCity() {
    const that = this;

    this.monitorService.getZoneDefault().subscribe({
      next: function (val) {
        that.map_model.cityList = val.regions;
        // that.zoom = that.switchZone(val.zone.level);
        that.node = that.getNode(val.regions, val.zone.region_id);
        that.map_model.currentCity = that.node;
        that.map_model.currentChildren = that.node.children;
      },
      complete: function () {
        // that.addBeiduMap(); // 

      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // 
  jumpHandle() {
    this.router.navigate([`home/airreport`]);

  }
  // 
  onIndexChange() {
    this.currentAirIndex = this.indexofHtml.name;
    this.map.clearOverlays();
    this.addCertainMarker(this.model.airdevicelist, this.currentAirIndex); // 
    // this.getAirdevices();
  }

  // -
  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 19;
        break;
      default:
        break;
    }
    return zone;
  }
  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }

      const obj = json[i];

      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;

        break;
      } else {

        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;

          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }
    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }

    // 6.obj
    // return {
    //   parentNode: that.parentNode,
    //   node: that.node
    // };
    return that.node;
  }
  // - 
  getPoint(baiduMap, city) {
    const zoom = this.switchZone(city.level);
    console.log(city);
    const pt = city.center;
    const point = new BMap.Point(pt.lng, pt.lat);
    baiduMap.centerAndZoom(point, zoom);
  }
  // 
  // 
  selecteCity(city) {
    this.map_model.currentCity = city;
    this.node = city;
    this.getPoint(this.map, city);  // - 
    this.map_model.currentChildren = city.children;
  }

  selecteblock(block) {
    this.getPoint(this.map, block);  // - 
    this.map_model.currentArea = block;
  }

  // 
  showArea() {
    this.areashow = true;
  }
  // 
  showCiyt() {
    this.cityshow = true;
  }
  // 
  citylistMouseleave() {
    this.cityshow = false;
  }
  // 
  arealistMouseover(area) {

    this.map_model.currentBlock = area.children;
  }

  // 
  arealistMouseleave() {
    this.areashow = false;
    this.map_model.currentBlock = [];
  }

  arealistMouseNone() {
    this.areashow = true;
    this.map_model.currentBlock = [];
  }

  ngOnDestroy() {
    window.clearInterval(this.timer);
  }

}
