import { Input, Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { AdminService } from '../../../service/admin.service';


declare var $: any;
@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {

  zTreeObj: any;

  setting = {}; // zTree  API setting 

      // zTree  API zTreeNode 
  zNodes = [
    {
      name: '', open: true, children: [
        { name: 'test1_1' }, { name: 'test1_2' }]
    },
    {
      name: '', open: true, children: [
        { name: 'test2_1' }, { name: 'test2_2' }]
    },
    {
      name: '', open: true, children: [
        { name: '' }, { name: '' }, { name: '' }]
    },
    {
      name: '', open: true, children: [
        {
          name: '', children: [
            { name: '' }, { name: '' }, { name: '' }
          ]
        },
        {
          name: '', children: [
            { name: '' }, { name: '' }, { name: '' }
          ]
        }
      ]
    }
  ];

  public mr: NgbModalRef; // 
  modelData = {
    title: '',
    body: 'hh',
  };
  genderData = [
    {name: '', value: 0},
    {name: '', value: 1}
  ];

  user: any = {}; // 
  closeResult: string;
  userList = []; // 
  roleList = []; // 
  roleList1 = []; // 
  queryStr = ''; // 
  page: any;
  pageSize = 10;
  total: number;

  curRole: any; // 
  addOrUpdate: any; // 
  @Input()
  public alerts: Array<IAlert> = [];
  public alertsModal: Array<IAlert> = [];

  constructor(private modalService: NgbModal, private adminService: AdminService) {
    this.page = 1;
  }

  ngOnInit() {
    this.zTreeObj = $.fn.zTree.init($('#treeDemo'), this.setting, this.zNodes);
    this.getUserList();
    this.getRoleList();
  }
  // 
  getUserList() {
    const that = this;
    this.adminService.getAllUser(this.queryStr, this.page, this.pageSize).subscribe({
      next: function(val) {
        console.log(val);
        that.userList = val.items;
        that.total = val.total;
      },
      complete: function() {},
      error: function(error) {
        // console.log('errorOnAdminpage');
        console.log(error);
      }
    });
  }
  // 
  getRoleList() {
    const that = this;
    this.adminService.getAllRole().subscribe({
      next: function(val) {
        that.roleList1 = val;
        that.roleList = val.map((item) => Object.assign({}, item));
        that.roleList.unshift({ id: 0, name: '' }); // 
        that.curRole = that.roleList[0];
      },
      complete: function() {},
      error: function(error) {
        console.log(error);
      }
    });
  }
  // 
  pageChange() {
    this.getUserList();
  }
  // 
  roleChange() {
    // console.log(this.curRole);
  }
  // 
  execQuery() {
    this.getUserList();
  }
  //  
  openAddUser(content) {
    const that = this;
    this.addOrUpdate = '';
    this.user.userName = '';
    this.user.password = '';
    this.user.email = '';
    this.user.mobile = '';
    this.user.fullName = '';
    this.user.nickName = '';
    this.user.gender = '';
    this.user.avatar = '';

    this.user.roleListCheck = []; // check
    this.user.roleIds = [];
    this.roleList1.map((item, i) => {
      that.user.roleListCheck.push({check: false}); // roleList1
    });

    const modal = this.modalService.open(content, { windowClass: 'md' });
    this.mr = modal;
    modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log(this.closeResult);
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(this.closeResult);
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
  // 
  addUser() {
    const that = this;
    this.adminService.addNewUser(this.user.userName, this.user.password, this.user.gender.value, this.user.avatar,
      this.user.email, this.user.mobile, this.user.fullName, this.user.nickName, this.user.roleIds).subscribe({
      next: function(val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
      },
      complete: function() {
        that.mr.close();
        that.getUserList();
      },
      error: function(error) {
        const message = error.json().errors[0].defaultMessage;
        that.alertsModal.push({
          id: 1,
          type: 'danger',
          message: `${message}`,
        });
        console.log(error);
      }
    });
  }
  //  
  openUpdateUser(content, item) {
    console.log(item);
    const that = this;
    this.addOrUpdate = '';
    this.user.curUser = item; // 
    this.user.userName = item.userName;
    this.user.password = item.password;
    this.user.email = item.email;
    this.user.mobile = item.mobile;
    this.user.fullName = item.fullName;
    this.user.nickName = item.nickName;
    // this.user.gender = item.gender;
    if (item.gender === 0) {
      this.user.gender = this.genderData[0];
    } else {
      this.user.gender = this.genderData[1];
    }
    this.user.avatar = item.avatarurl;

    this.user.roleListCheck = []; // check
    this.user.roleIds = [];
    this.roleList1.map((item1, i) => {
      that.user.roleListCheck.push({check: false}); // roleList1
    });

    const modal = this.modalService.open(content, { windowClass: 'md' });
    this.mr = modal;
    modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log(this.closeResult);
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(this.closeResult);
    });
  }
  // 
  updataUser() {
    const that = this;
    this.adminService.updateUser(this.user.curUser.id, this.user.userName, this.user.password, this.user.gender.value, this.user.avatar,
      this.user.email, this.user.mobile, this.user.fullName, this.user.nickName, this.user.roleIds).subscribe({
      next: function(val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
      },
      complete: function() {
        that.mr.close();
        that.getUserList();
      },
      error: function(error) {
        const message = error.json().errors[0].defaultMessage;
        that.alertsModal.push({
          id: 1,
          type: 'danger',
          message: `${message}`,
        });
        console.log(error);
      }
    });
  }
  // /
  addorUpdt() {
    if (this.addOrUpdate === '') {
      this.addUser();
    } else {
      this.updataUser();
    }
  }
  // 
  openDelUser(content, item) {
    const that = this;
    this.user.itemDelId = item.id;
    const modal = this.modalService.open(content, { size: 'sm' });
    this.mr = modal;
  }

  // 
  closeUser($event) {
    console.log($event);
    if ($event === 'ok') {
      this.delUser();
    }
    this.mr.close();
  }
  // -
  delUser() {
    const that = this;
    const id = this.user.itemDelId;
    let flag = false;
    const pages = (this.total + this.pageSize - 1) / this.pageSize;
    if (this.page >= pages && this.userList.length === 1) {
      flag = true;
    }
    this.adminService.deleteUser(id).subscribe({
      next: function (val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
      },
      complete: function () {
        if (flag) {
          that.page  = that.page - 1;
          that.getUserList();
        } else {
          that.getUserList();
        }
      },
      error: function (error) {
        console.log(error);
      }
    });
  }
  // 
  addRoleToUser() {
    const that = this;
    this.user.roleIds = [];
    this.user.roleListCheck.map((item, i) => {
      if (item.check === true) {
        const item1 = that.roleList1[i].id;
        if (item1) {
          that.user.roleIds.push(item1);
        }
      }
    });
    console.log(this.user.roleIds);
  }
  public closeAlert(alert: IAlert) {
    const index: number = this.alerts.indexOf(alert);
    this.alerts.splice(index, 1);
  }
  public closeAlertModal(alert: IAlert) {
    const index: number = this.alertsModal.indexOf(alert);
    this.alertsModal.splice(index, 1);
  }

}

export interface IAlert {
  id: number;
  type: string;
  message: string;
}
