import { Component, OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { MonitorService } from '../../../service/monitor.service';
import { VideoService } from '../../../service/video.service';
// baidu map
declare let BMap;
declare let BMAP_ANCHOR_TOP_LEFT;

@Component({
  selector: 'app-calamity',
  templateUrl: './calamity.component.html',
  styleUrls: ['./calamity.component.scss']
})
export class CalamityComponent implements OnInit, OnDestroy {

  @ViewChild('map5') map_container: ElementRef;
  /*
  model:object
  light_list: array // 
  */
  model: any = {}; // 

  /*
  map_model: object // 
  @currentCity: any // 
  @currentArea: any // 
  @cityList: array // 
  @currentChildren: array // 
  @currentBlock: array //  = []; // 2
  */

  map_model: any = {}; // 

  map: any; // 
  timer: any; // 

  areashow = false; // 
  cityshow = false; // 
  deviceshow = false; // 

  visible = true; // 

  parentNode = null; // JSON 
  node = null; // JSON 


  constructor(private monitorService: MonitorService, private videoService: VideoService ) {
    this.model.light_list = []; // 
    this.map_model.cityList = []; // 
    this.map_model.currentChildren = []; // 
    this.map_model.currentBlock = []; // //  = []; // 2
   }

  ngOnInit() {
    this.addBeiduMap();
    this.getCity(); // 

  }



  // API
  addBeiduMap() {

    const map = this.map = new BMap.Map(this.map_container.nativeElement, {
      enableMapClick: true,
      // minZoom: 11,
      // maxZoom : 11
    }); // 


    // BMapPointPoint116.40439.915

    const point = new BMap.Point(113.924755, 22.49934); //  --
    map.centerAndZoom(point, 16); // 

    map.enableScrollWheelZoom(true); // 



    map.setMapStyle({ style: 'normal' });   // dark


    // 

    const offset = new BMap.Size(20, 55);
    const navigationControl = new BMap.NavigationControl({
      anchor: BMAP_ANCHOR_TOP_LEFT,
      offset: offset,
    });
    map.addControl(navigationControl);



    this.getLights(); // 
    this.timer = setInterval(() => {
      this.getLights(); // 
    }, 10000);



  }


  getLights() {
    const that = this;

    this.videoService.getCalamity().subscribe({
      next: function (val) {
        that.model.light_list = val;
        const compar = that.judgeChange(that.model.light_list);
        console.log(compar);
        that.deleMarker(compar.a0); // 
        that.changeMarker(compar.b1); // 

      },
      complete: function () {
      },
      error: function (error) {
        console.log(error);
      }
    });



  }
  // 
  judgeChange(array) {
    const a0 = [];
    const b1 = [];
    for (let index = 0; index < array.length; index++) {
      const element = array[index];
      if (element.error === 1) {
        b1.push(element);
      } else {
        a0.push(element);
      }
    }
    return {
      a0: a0,
      b1: b1
    };
  }




  // 
  changeMarker(light_list) {
    this.deleMarker(light_list); // 
    this.addMarker(light_list); // 
  }
  // 
  deleMarker(light_list) {
    const makers = this.map.getOverlays();
    for (let ind = 0; ind < light_list.length; ind++) {
      const ele = light_list[ind];
      const point = light_list[ind].point;
      for (let index = 0; index < makers.length; index++) {
        const element = makers[index];
        const lat = element.point && element.point.lat;
        const lng = element.point && element.point.lng;
        if (point[1] === lat && point[0] === lng) {
          this.map.removeOverlay(makers[index]);
        }

      }
    }
  }

  addMarker(light_list) {
    for (let index = 0; index < light_list.length; index++) {
      const item = light_list[index];
      const point = new BMap.Point(item.point[0], item.point[1]);

      let myIcon;
      if (item.type === 1 && item.error === 1) { // 1building
        myIcon = new BMap.Icon('../../../../assets/imgs/building.gif', new BMap.Size(300, 157));
        myIcon.setAnchor(new BMap.Size(16, 38));
        const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
        this.map.addOverlay(marker2);
      } else if (item.type === 2 && item.error === 1) { // 2landslide
        myIcon = new BMap.Icon('../../../../assets/imgs/landslide.gif', new BMap.Size(300, 157));
        myIcon.setAnchor(new BMap.Size(16, 38));
        const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
        this.map.addOverlay(marker2);

      } else if (item.type === 3 && item.error === 1) { // 1odor
        myIcon = new BMap.Icon('../../../../assets/imgs/odor.gif', new BMap.Size(300, 157));
        myIcon.setAnchor(new BMap.Size(16, 38));
        const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
        this.map.addOverlay(marker2);

      } else if (item.type === 4 && item.error === 1) { // 1gas
        myIcon = new BMap.Icon('../../../../assets/imgs/gas.gif', new BMap.Size(300, 157));
        myIcon.setAnchor(new BMap.Size(16, 38));
        const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
        this.map.addOverlay(marker2);

      } else if (item.type === 5 && item.error === 1) { // 1hydrops
        myIcon = new BMap.Icon('../../../../assets/imgs/hydrops.gif', new BMap.Size(300, 157));
        myIcon.setAnchor(new BMap.Size(16, 38));
        const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
        this.map.addOverlay(marker2);

      }

    }
  }

  // - 
  getPoint(baiduMap, city) {
    const zoom = this.switchZone(city.level);
    console.log(city);
    const pt = city.center;
    const point = new BMap.Point(pt.lng, pt.lat);
    baiduMap.centerAndZoom(point, zoom);
  }

  // 

  //  --ok
  getCity() {
    const that = this;

    this.monitorService.getZoneDefault().subscribe({
      next: function (val) {
        that.map_model.cityList = val.regions;
        // that.zoom = that.switchZone(val.zone.level);
        that.node = that.getNode(val.regions, val.zone.region_id);
        that.map_model.currentCity = that.node;
        that.map_model.currentChildren = that.node.children;

      },
      complete: function () {

      },
      error: function (error) {
        console.log(error);
      }
    });
  }


  // -
  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 19;
        break;
      default:
        break;
    }
    return zone;
  }

  //
  /*
   * JSON 
   */


  /**
   * NodeID
   *
   * @param  {[type]}
   * @param  {[type]}
   * @return {[type]}
   */

  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }

      const obj = json[i];

      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;

        break;
      } else {

        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;

          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }


    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }

    return that.node;
  }


  // 
  enterFullScreen() {
    console.log('');
    console.log(this.visible);
    // this.visible = false;
    // localStorage.setItem('visible', 'false');

    // 
    // const offset = new BMap.Size(20, 15);
    // this.navigationControl.setOffset(offset);

    // this.communicateService.sendMessage(this.visible); // 
    // this.fullScreenService.enterFullScreen();

  }


  // 
  // 
  selecteCity(city) {
    this.map_model.currentCity = city;
    this.getPoint(this.map, city);  // - 
    this.map_model.currentChildren = city.children;
  }

  selecteblock(block) {
    this.getPoint(this.map, block);  // - 
    this.map_model.currentArea = block;
  }

  // 
  showArea() {
    this.areashow = true;
  }
  // 
  showCiyt() {
    this.cityshow = true;
  }
  // 
  showDevice() {
    this.deviceshow = true;
  }

  // 
  arealistMouseover(area) {

    this.map_model.currentBlock = area.children;
  }
  // 
  arealistMouseleave() {
    this.areashow = false;
    this.map_model.currentBlock = [];
  }
  // 
  citylistMouseleave() {
    this.cityshow = false;
  }
  // 
  devicelistMouseleave() {
    this.deviceshow = false;
  }
  arealistMouseNone() {
    this.areashow = true;
    this.map_model.currentBlock = [];
  }

  ngOnDestroy() {
    window.clearInterval(this.timer);
  }
}
