import { Input, Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { MonitorService } from '../../../service/monitor.service';
import { DeviceService } from '../../../service/device.service';
import { GradOverlar } from '../../../service/grad.overlay';


declare let BMap;


@Component({
  selector: 'app-devices',
  templateUrl: './devices.component.html',
  styleUrls: ['./devices.component.scss']
})
export class DevicesComponent implements OnInit {
  closeResult: string;
  map: any; // 

  cityList: any; // 
  deviceList: any; // 
  defaultZone: any; // 
  currentCity: any; // 
  currentArea: any; // 
  currentChildren: any; // 
  currentBlock: any; // // 
  areashow = false; // 
  cityshow = false; // 
  deviceshow = false; // 

  visible = true; // 

  zoom: any; // 
  SouthWest: any; // 
  NorthEast: any; // 
  parentNode = null; // JSON 
  node = null; // JSON 

  deviceslist = [];  // 
  page: any;
  pageSize = 10;
  total: number;
  deviceModels = [];  // 
  deviceModels1 = [];
  currentModel: any; // 
  queryStr: any; // 
  queryStrPosi: any; // 

  device: any = {}; // 
  public mr: NgbModalRef; // 
  modelData = {
    title: '',
    body: 'hh',
  };
  posiListByRegion = []; // 
  pagePosi: any;
  pageSizePosi = 10;
  total1: number;
  showPosiTable = false; // 
  bindedPosition: any; // 

  addOrUpdate: any; // /
  curModelIndex: any; // 

  @Input()
  public alerts: Array<IAlert> = [];
  public alertsModal: Array<IAlert> = [];

  private backup: Array<IAlert>;

  constructor(private modalService: NgbModal, private monitorService: MonitorService,
    private deviceService: DeviceService) {

    this.page = 1;
    this.pagePosi = 1;
    this.curModelIndex = 0; // 
    this.queryStr = '';
    this.queryStrPosi = '';
    this.device.point = {lng: '', lat: ''};
  }

  public closeAlert(alert: IAlert) {
    const index: number = this.alerts.indexOf(alert);
    this.alerts.splice(index, 1);
  }
  public closeAlertModal(alert: IAlert) {
    const index: number = this.alertsModal.indexOf(alert);
    this.alertsModal.splice(index, 1);
  }

  ngOnInit() {
    this.getCity();
    // this.getDevice();
    this.getAllDeviceModel();
    // this.getCityDropdownListgetCityDropdownList();
    this.getDevicesList(this.page, this.pageSize);
  }

  // 
  getAllDeviceModel() {
    const that = this;
    this.deviceService.getAllDeviceModel(0, 1, 20).subscribe({
      next: function (val) {
        that.deviceModels1 = val.items;
        that.deviceModels = val.items.map((item) => Object.assign({}, item));
        that.deviceModels.unshift({ id: 0, name: '' }); // 
        that.currentModel = that.deviceModels[0]; // 
        that.curModelIndex = that.currentModel.id; // 
        that.device.model = that.deviceModels1[0];
      },
      complete: function () { },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // 
  getDevicesList(page, pageSize) {
    const that = this;
    this.deviceService.getAllDeviceByModel(this.queryStr, this.curModelIndex, page, pageSize).subscribe({
      next: function (val) {
        that.deviceslist = val.items;
        that.total = val.total;
        // console.log(that.deviceslist);
      },
      complete: function () { },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // 
  deviceTypeChange() {
    this.curModelIndex = this.currentModel.id;
    this.getDevicesList(this.page, this.pageSize);
    // 
  }
  // 
  execQuery() {
    this.getDevicesList(this.page, this.pageSize);
  }
  // 
  pageChange() {
    this.getDevicesList(this.page, this.pageSize);
  }
  pageChangePosi() {
    this.getPosiByRegionId(this.currentArea.id, this.pagePosi, this.pageSizePosi);
  }

  // 
  openAddSurveys(content) {
    const that = this;
    this.modalService.open(content, { windowClass: 'md-modal' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log(this.closeResult);

    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(this.closeResult);
    });
  }
  // 
  openNewSurvey(content) {
    this.addOrUpdate = '';
    this.device.name = '';
    this.device.model = this.deviceModels1[0];
    this.device.descr = '';
    this.device.bindedPosi = this.bindedPosition;
    this.bindedPosition = null;

    const modal = this.modalService.open(content, { windowClass: 'ex-lg-modal' });
    this.mr = modal;
    this.addBaiduMap();

    modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;

      // this.addDevice();
      this.showPosiTable = false;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      this.showPosiTable = false;
    });
  }
  addorUpdate(addOrUpdate) {
    if (addOrUpdate === '') {
      this.addDevice();
    } else {
      this.device.modelId = this.device.model.id; // modelIddevice.model
      this.updataDevice();
    }
  }
  // 
  addDevice() {
    const that = this;
    const name = this.device.name;
    const modelId = this.device.model.id;
    const descr = this.device.descr;
    const bindedPosi = this.bindedPosition;
    const lng = bindedPosi.point.lng;
    const lat = bindedPosi.point.lat;
    const posiId = bindedPosi.id;

    this.deviceService.addNewDevice(name, modelId, descr, posiId, lng, lat).subscribe({
      next: function (val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
        that.backup = that.alerts.map((alert: IAlert) => Object.assign({}, alert));
        that.mr.close();
      },
      complete: function () {
        that.getDevicesList(that.page, that.pageSize);
      },
      error: function (error) {
        const message = error.json().errors[0].defaultMessage;
        that.alertsModal.push({
          id: 1,
          type: 'danger',
          message: `: ${message}`,
        });
        console.log(error.json());
      }
    });
  }
  // 
  openUpdataDevice(content, item, i) {
    this.queryStrPosi = '';
    this.addOrUpdate = '';
    const that = this;
    this.getPosiById(item.positionId); // device.positionId -> position. (->)

    this.device.updateId = item.id;
    this.device.name = item.name;
    this.device.point = item.point;
    const id = item.modelId;

    this.device.descr = item.description;
    // 
    for (let index = 0; index < this.deviceModels1.length; index++) {
      const element = that.deviceModels1[index];
      if (id === element.id) {
        that.device.model = that.deviceModels1[index]; // item
        break;
      }
    }

    const modal = this.modalService.open(content, { windowClass: 'ex-lg-modal' });
    this.mr = modal;
    this.addBaiduMap();

    modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      // console.log(this.closeResult);
      // this.device.modelId = this.device.model.id; // modelIddevice.model
      // that.updataDevice();
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(this.closeResult);
    });
  }
  // 
  updataDevice() {
    const that = this;
    const id = this.device.updateId;
    const name = this.device.name;
    const modelId = this.device.modelId;
    const descr = this.device.descr;

    const bindedPosi = this.bindedPosition;
    const lng = bindedPosi.point.lng;
    const lat = bindedPosi.point.lat;
    const posiId = bindedPosi.id;

    this.deviceService.updateDevice(id, name, modelId, descr, posiId, lng, lat).subscribe({
      next: function (val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
        that.backup = that.alerts.map((alert: IAlert) => Object.assign({}, alert));
        that.mr.close();
      },
      complete: function () {
        that.getDevicesList(that.page, that.pageSize);
      },
      error: function (error) {
        const message = error.json().errors[0].defaultMessage;
        that.alertsModal.push({
          id: 1,
          type: 'danger',
          message: `: ${message}`,
        });
        console.log(error.json());
        console.log(error);
      }
    });
  }
  // 
  openDelDevice(content, item) {
    const that = this;
    this.device.itemDelId = item.id;
    const modal = this.modalService.open(content, { size: 'sm' });
    this.mr = modal;
  }
  // 
  closeDevice($event) {
    console.log($event);
    if ($event === 'ok') {
      this.delDevice();
    }
    this.mr.close();
  }
  // -
  delDevice() {
    const that = this;
    const id = this.device.itemDelId;
    let flag = false;
    const pages = (this.total + this.pageSize - 1) / this.pageSize;
    if (this.page >= pages && this.deviceslist.length === 1) {
      flag = true;
    }
    this.deviceService.delDevice(id).subscribe({
      next: function (val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
        that.backup = that.alerts.map((alert: IAlert) => Object.assign({}, alert));
      },
      complete: function () {
        if (flag) {
          that.page  = that.page - 1;
          that.getDevicesList(that.page, that.pageSize);
        } else {
          that.getDevicesList(that.page, that.pageSize);
        }
      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // position
  bindPosition(position) {
    this.map.clearOverlays();
    this.bindedPosition = position;
    const point = new BMap.Point(position.point.lng, position.point.lat);
    this.map.centerAndZoom(point, 18);
    const mySquare = new GradOverlar(point, 50, 'tag-bule');
    this.map.addOverlay(mySquare);
  }


  addBaiduMap() {
    const map = this.map = new BMap.Map('survey_map', {
      enableMapClick: true,
      // minZoom: 11
    }); // 
    const point = new BMap.Point(113.922329, 22.49656); //  --
    map.centerAndZoom(point, 17); // 
    map.enableScrollWheelZoom(true); // 
    map.setMapStyle({ style: 'normal' });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  // id
  modelName(modelId) {
    let modelName = null;
    this.deviceModels.map((item, i) => {
      if (item.id === modelId) {
        modelName = item.name;
      }
    });
    // console.log(modelName);
    return modelName;
  }

  // positionId
  getPosiById(id) {
    const that = this;
    let curPosition;
    this.deviceService.getPosiById(id).subscribe({
      next: function (val) {
        curPosition = val;
        that.bindedPosition = val;
        console.log(val);
      },
      complete: function () {
        that.updatePosiRegion();
        that.bindPosition(curPosition);
        that.getPosiByRegionId(curPosition.regionId, 1, that.pageSizePosi);
      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // ''
  updatePosiRegion() {

    const that = this;
    let region_id; // id
    const crrentProvince = this.cityList[0]; // 
    for (let index = 0; index < crrentProvince.children.length; index++) {
      const element = crrentProvince.children[index];
      if (that.bindedPosition.installZoneId === element.installZoneId) {
        region_id = element.id;
      }
    }

    that.node = null; // JSON 
    that.currentCity = that.getNode(that.cityList, region_id); // 
    that.currentChildren = that.node.children; // 

    const area_id = that.bindedPosition.regionId; // id
    that.node = null; // JSON 
    that.currentArea = that.getNode(that.cityList, area_id); // i
  }

  getCity() {
    const that = this;

    this.deviceService.getZoneDefault().subscribe({
      next: function (val) {
        that.cityList = val.regions;
        // that.currentCity = val.zone;
        that.zoom = that.switchZone(val.zone.level);
        that.node = that.getNode(val.regions, val.zone.region_id);
        that.currentCity = that.node;
        that.currentChildren = that.node.children;

      },
      complete: function () {
        // that.addBaiduMap(); // 

      },
      error: function (error) {
        console.log(error);
      }
    });
  }
  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 17;
        break;
      default:
        break;
    }
    return zone;
  }
  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }
      const obj = json[i];
      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;
        break;
      } else {
        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;
          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }
    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }
    // 6.obj
    // return {
    //   parentNode: that.parentNode,
    //   node: that.node
    // };
    return that.node;
  }
  getPoint(baiduMap, city) {
    const that = this;
    // 
    const myGeo = new BMap.Geocoder();
    const zoom = this.zoom = this.switchZone(city.level);
    const fullName = city.full_name;
    const pt = city.center;
    const point = new BMap.Point(pt.lng, pt.lat);
    baiduMap.centerAndZoom(point, zoom);



  }

  // 
  // 
  selecteCity(city) {
    this.device.installZoneId = city.installZoneId; // 
    this.device.point = { lng: '', lat: '' };
    this.currentCity = city;
    this.currentChildren = city.children;
    this.currentArea = null;

    this.node = city;
    this.getPoint(this.map, city);  // - 
  }

  // 
  selecteblock(block) {
    this.queryStrPosi = '';
    this.getPoint(this.map, block);  // - 
    this.currentArea = block;
    this.device.point = { lng: '', lat: '' };
    this.pagePosi = 1;
    this.getPosiByRegionId(this.currentArea.id, this.pagePosi, this.pageSizePosi);
  }
  // /
  execQueryPosi() {
    this.pagePosi = 1;
    this.getPosiByRegionId(this.currentArea.id, this.pagePosi, this.pageSizePosi);
  }
  // 
  getPosiByRegionId(regionId, page, pageSize) {
    const that = this;
    this.showPosiTable = true;
    this.deviceService.getAllPosiByRegionId(this.queryStrPosi, regionId, page, pageSize).subscribe({
      next: function (val) {
        that.posiListByRegion = val.items;
        that.total1 = val.total;
      },
      complete: function () {},
      error: function (error) {
        console.log(error);
      }
    });
  }
  // 
  showArea() {
    this.areashow = true;
  }
  // 
  showCiyt() {
    this.cityshow = true;
  }
  // 
  arealistMouseover(area) {

    this.currentBlock = area.children;
  }
  // 
  arealistMouseleave() {
    this.areashow = false;
    this.currentBlock = null;
  }
  // 
  citylistMouseleave() {
    this.cityshow = false;
  }
  arealistMouseNone() {
    this.areashow = true;
    this.currentBlock = null;
  }
}

export interface IAlert {
  id: number;
  type: string;
  message: string;
}
