import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { MonitorService } from '../../../service/monitor.service';
import { CameraService } from '../../../service/camera.service';

// baidu map
declare let Aliplayer;
declare let BMap;
declare let BMapLib;
declare let BMAP_ANCHOR_TOP_RIGHT;



@Component({
  selector: 'app-traffic',
  templateUrl: './traffic.component.html',
  styleUrls: ['./traffic.component.scss']
})
export class TrafficComponent implements OnInit {

  @ViewChild('map4') map_container: ElementRef;
  /*
 model:object
 light_list: array // 
 */
  model: any = {}; // 

  /*
  map_model: object // 
  @currentCity: any // 
  @currentArea: any // 
  @cityList: array // 
  @currentChildren: array // 
  @currentBlock: array //  = []; // 2
  */

  map_model: any = {}; // 

  map: any; // 

  currentCamera: any;  // 

  currentCameraShowMe = false;

  visible = true; // 
  areashow = false; // 
  cityshow = false; // 

  parentNode = null; // JSON 
  node = null; // JSON 


  constructor(private monitorService: MonitorService, public router: Router,
    private cameraService: CameraService) {
    this.model.light_list = []; // 
    this.map_model.cityList = []; // 
    this.map_model.currentChildren = []; // 
    this.map_model.currentBlock = []; // //  = []; // 2
    }

  ngOnInit() {
    this.addBeiduMap();
    this.getCity(); // 
  }

  // API
  addBeiduMap() {

    const map = this.map = new BMap.Map(this.map_container.nativeElement, {
      enableMapClick: true,
      minZoom: 11,
      // maxZoom : 11
    }); // 


    // BMapPointPoint116.40439.915
    const point = new BMap.Point(113.922329, 22.49656); //  --
    map.centerAndZoom(point, 15); // 
    map.setMapStyle({ style: 'googlelite' });

    // 

    const offset = new BMap.Size(100, 20);
    const navigationControl = new BMap.NavigationControl({
      anchor: BMAP_ANCHOR_TOP_RIGHT,
      offset: offset,
    });
    map.addControl(navigationControl);

    const ctrl = new BMapLib.TrafficControl({
      showPanel: true , // 
    });
    map.addControl(ctrl);
    ctrl.showTraffic({ predictDate: { hour: 15, weekday: 5 } });
    ctrl.setAnchor(BMAP_ANCHOR_TOP_RIGHT);
    ctrl.setOffset(new BMap.Size(180, 20));

    map.enableScrollWheelZoom(true); // 

    // const marker = new BMap.Marker(point);  // 
    // map.addOverlay(marker);               // 
    this.addMarker();
    this.dragendOff(map);
    this.zoomendOff(map);
    this.mapClickOff(map); // 
  }
  // --
  dragendOff(baiduMap) {
    const that = this;
    baiduMap.addEventListener('dragend', function () {
      baiduMap.clearOverlays();
      that.addMarker(); // -
    });
  }
  // --
  zoomendOff(baiduMap) {
    const that = this;
    baiduMap.addEventListener('zoomend', function () {
      // if (that.isqueryPoint === true) {
      //   that.isqueryPoint = false;
      // } else {
        baiduMap.clearOverlays();
        that.addMarker(); // 
        // console.log('' + baiduMap.getZoom() + '');
      // }
    });
  }
  // -
  mapClickOff(baiduMap) {
    const that = this;
    baiduMap.addEventListener('click', function (e) {
      // that.currentCameraShow = false;
    });
  }
  // 
  addMarker() {
    this.getCameras();
  }
  // 
  getCameras() {
    const that = this;
    const Bounds = this.map.getBounds(); // 
    const NorthEast = Bounds.getNorthEast(); // 
    const SouthWest = Bounds.getSouthWest(); // 

    let value;
    this.cameraService.getCameras(NorthEast, SouthWest).subscribe({
      next: function (val) {
        value = val;
      },
      complete: function () {
        that.addPoint(value);
      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // 
  addPoint(val) {
    const markers: any[] = [];
    const points: any[] = [];
    val.map((item, i) => {
      const point = new BMap.Point(item.point.lng, item.point.lat);
      // const name = item.name;
      // 
      let myIcon;

      if (item.error === true) { // 
        myIcon = new BMap.Icon('../../../../assets/imgs/camera_error.png', new BMap.Size(300, 157));
      } else if (item.offline === true) { // 
        myIcon = new BMap.Icon('../../../../assets/imgs/camera_offline.png', new BMap.Size(300, 157));
      } else { // 
        myIcon = new BMap.Icon('../../../../assets/imgs/camera_normal.png', new BMap.Size(300, 157));
      }
      myIcon.setAnchor(new BMap.Size(16, 38));
      const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
      this.map.addOverlay(marker2);
      markers.push(marker2); // 
      points.push(point); // 
    });

    // 
    for (let index = 0; index < markers.length; index++) {
      const marker = markers[index];
      this.openSideBar(marker, val[index]);
    }
  }

  // -
  openSideBar(marker, camera) {
    console.log(camera);
    const that = this;

    let txt = `<p style='font-size: 12px; line-height: 1.8em; border-bottom: 1px solid #ccc; padding-bottom: 10px;'>`;

    txt = txt + ` | ${camera.positionNumber} </p><p> ${camera.description}</p>`;
    if (camera.offline === false) {
      txt = txt + `<p> </p>`;
    } else {
      txt = txt + `<p> <span style='color: red'></span></p>`;
    }
    if (camera.error === false) {
      txt = txt + `<p> </p>`;
    } else {
      txt = txt + `<p> <span style='color: red'></span></p>`;
    }
    txt = txt + `<button class='btn btn-bg' style='font-size: 14px; float: right; margin: 5px' id='${camera.id}'></button>`;

    // const infoWindow = new BMap.InfoWindow(txt, opts);

    marker.addEventListener('click', function () {
      that.currentCamera = camera;
      that.currentCameraShowMe = true;

      setTimeout(() => {
        that.cameraAddEventListener();
      }, 2);
    });

  }
  // 
  cameraAddEventListener() {
    const that = this;
    // let player;
    setTimeout(() => {
      const player = new Aliplayer({
        'id': 'video_play',
        'source': that.currentCamera.videoUrl,
        'width': '100%',
        'height': '500px',
        'autoplay': true,
        'isLive': false,
        'rePlay': false,
        'playsinline': true,
        'preload': true,
        'controlBarVisibility': 'hover',
        'useH5Prism': true
      }, function (play) {
        console.log('');
      }
      );
    }, 2);

      // const device = $(`#${this.Camera.id}`);
      // device.on('click', function () {
      //   console.log('ddd');
      //   that.cameraChild = that.Camera;
      //   let player;
      //   setTimeout(() => {
      //     player = new Aliplayer({
      //       'id': 'video_play',
      //       'source': that.Camera.videoUrl,
      //       'width': '100%',
      //       'height': '500px',
      //       'autoplay': true,
      //       'isLive': false,
      //       'rePlay': false,
      //       'playsinline': true,
      //       'preload': true,
      //       'controlBarVisibility': 'hover',
      //       'useH5Prism': true
      //     }, function (play) {
      //       console.log('');
      //     }
      //     );
      //   }, 2);
      // });

  }
  // 
  closeDetail() {
    this.currentCameraShowMe = false;
  }

  // 
  // getBounds(baiduMap) {
  //   const Bounds = baiduMap.getBounds(); // 
  //   this.NorthEast = Bounds.getNorthEast(); // 
  //   this.SouthWest = Bounds.getSouthWest(); // 
  //   this.zoom = baiduMap.getZoom(); // 
  // }
  //  --ok
  getCity() {
    const that = this;

    this.monitorService.getZoneDefault().subscribe({
      next: function (val) {
        that.map_model.cityList = val.regions;
        // that.zoom = that.switchZone(val.zone.level);
        that.node = that.getNode(val.regions, val.zone.region_id);
        that.map_model.currentCity = that.node;
        that.map_model.currentChildren = that.node.children;

      },
      complete: function () {
        that.addBeiduMap(); // 

      },
      error: function (error) {
        console.log(error);
      }
    });
  }
  // -
  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 19;
        break;
      default:
        break;
    }
    return zone;
  }

  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }

      const obj = json[i];

      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;

        break;
      } else {

        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;

          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }


    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }

    // 6.obj
    // return {
    //   parentNode: that.parentNode,
    //   node: that.node
    // };
    return that.node;
  }



  // - 
  getPoint(baiduMap, city) {
    const zoom = this.switchZone(city.level);
    console.log(city);
    const pt = city.center;
    const point = new BMap.Point(pt.lng, pt.lat);
    baiduMap.centerAndZoom(point, zoom);
  }
  // 
  // 
  selecteCity(city) {
    this.map_model.currentCity = city;
    this.node = city;
    this.getPoint(this.map, city);  // - 
    this.map_model.currentChildren = city.children;
  }

  selecteblock(block) {
    this.getPoint(this.map, block);  // - 
    this.map_model.currentArea = block;
  }

  // 
  showArea() {
    this.areashow = true;
  }
  // 
  showCiyt() {
    this.cityshow = true;
  }
  // 
  citylistMouseleave() {
    this.cityshow = false;
  }
  // 
  arealistMouseover(area) {

    this.map_model.currentBlock = area.children;
  }

  // 
  arealistMouseleave() {
    this.areashow = false;
    this.map_model.currentBlock = null;
  }

  arealistMouseNone() {
    this.areashow = true;
    this.map_model.currentBlock = null;
  }

}

