import { Input, Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NgbModal, ModalDismissReasons, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';
import { PositionService } from '../../../service/position.service';

// baidu map
declare let BMap;

@Component({
  selector: 'app-position',
  templateUrl: './position.component.html',
  styleUrls: ['./position.component.scss']
})

export class PositionComponent implements OnInit {
  @ViewChild('map1') map_container: ElementRef;
  model: any = {}; // 

  closeResult: string;
  map: any; // 

  cityList: any; // 
  deviceList = []; // 
  deviceTypes = []; // 
  currentType: any; // 
  defaultZone: any; // 
  currentCity: any; // 
  currentArea: any; // 
  currentChildren: any; // 
  currentBlockList: any; // // 
  areashow = false; // 
  cityshow = false; // 


  visible = true; // 

  zoom: any; // 

  parentNode = null; // JSON 
  node = null; // JSON ---

  positionListItems = []; // 
  positionList: any; // 
  total: number; // 
  page: number;
  pagesize = 10;
  queryStr: any;


  public mr: NgbModalRef; // 
  modelData = {
    title: '',
    body: 'hh',
  };

  errorMess = []; // 

  @Input()
  public alerts: Array<IAlert> = [];
  public alertsModal: Array<IAlert> = [];

  private backup: Array<IAlert>;


  constructor(private modalService: NgbModal, private positionService: PositionService) {
    this.queryStr = '';
    this.page = 1;
    this.model.point = {lng: '', lat: ''};
  }

  public closeAlert(alert: IAlert) {
    const index: number = this.alerts.indexOf(alert);
    this.alerts.splice(index, 1);
  }
  public closeAlertModal(alert: IAlert) {
    const index: number = this.alertsModal.indexOf(alert);
    this.alertsModal.splice(index, 1);
  }

  public reset() {
    this.alerts = this.backup.map((alert: IAlert) => Object.assign({}, alert));
  }

  ngOnInit() {
    this.getCity();
    this.getDevice();
    this.getPosition(0, this.page, this.pagesize);
  }
  // 
  execQuery() {
    this.page = 1;
    this.getPosition(this.currentType.id, 1, this.pagesize);
  }

  changeName(modelId) {
    let modelName;
    this.deviceTypes.map((item, i) => {
      if (item.id === modelId) {
        modelName = item.name;
      }
    });
    // console.log(modelName);
    return modelName;
  }

  deviceTypeChange() {
    this.getPosition(this.currentType.id, this.page, this.pagesize);
  }

  // 
  openNewPosition(content) {
    const that = this;
    this.errorMess = [];
    this.model.name = ''; // name
    this.model.number = ''; // number
    this.model.point = { lng: '', lat: '' }; // 

    that.currentChildren = that.currentCity.children; // 
    that.model.installZoneId = that.currentCity.installZoneId; // 
    that.currentArea = that.currentChildren[0].children[0]; // 

    that.model.device = this.deviceList[0]; // 

    const modal = this.modalService.open(content, { size: 'lg' });
    this.mr = modal;
    this.addBaiduMap();

    modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      // console.log(this.closeResult);
      // that.setPosition();
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      // console.log(this.closeResult);
    });

  }


    // 
  setPosition() {
    this.typeofPoint();
    if (this.errorMess[0] === '') {
        const that = this;
        const installZoneId = this.model.installZoneId;
        const regionId = this.currentArea.id;
        const name = this.model.name;
        const number = this.model.number;
        const point = this.model.point;
        const type = this.model.device.id;

        this.positionService.setPosition(installZoneId, regionId, name, number, point, type).subscribe({
          next: function (val) {
            that.alerts.push({
              id: 1,
              type: 'success',
              message: '',
            });
            that.backup = that.alerts.map((alert: IAlert) => Object.assign({}, alert));
            that.mr.close();
          },
          complete: function () {
            that.getPosition(that.currentType.id, that.page, that.pagesize);
          },
          error: function (error) {
            const message = error.json().errors[0].defaultMessage;
            that.alertsModal.push({
              id: 1,
              type: 'danger',
              message: `: ${message}`,
            });
            console.log(error.json());
          }
        });
    } else {
      this.alertsModal.push({
        id: 1,
        type: 'danger',
        message: '',
      });
    }

  }

  // 
  openUpdataPosi(content, item, i) {
    const that = this;
    this.errorMess = [];
    this.model.updataId = item.id;
    // this.model.installZoneId = item.installZoneId;
    this.model.installZoneId = item.installZoneId; // 
    this.model.name = item.name; // name
    this.model.number = item.number; // number
    this.model.point = item.point; // point

    const id = item.type; // 
    for (let index = 0; index < this.deviceList.length; index++) {
      const element = this.deviceList[index];
      if (id === element.id) {
        that.model.device = this.deviceList[index];
      }
    }

    let region_id; // id
    const crrentProvince = this.cityList[0]; // 
    for (let index = 0; index < crrentProvince.children.length; index++) {
      const element = crrentProvince.children[index];
      if (item.installZoneId === element.installZoneId) {
        region_id = element.id;
      }
    }
    that.node = null; // JSON 
    that.currentCity = that.getNode(that.cityList, region_id); // 
    that.currentChildren = that.node.children; // 
    that.model.installZoneId = that.node.installZoneId; // 


    const area_id = item.regionId; // id
    that.node = null; // JSON 
    that.currentArea = that.getNode(that.cityList, area_id); // i

    const modal = this.modalService.open(content, { size: 'lg' });
    this.mr = modal;
    this.addBaiduMap();
    modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log(this.closeResult);
      // that.updataPosition();
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(this.closeResult);
    });
  }


  // 
  updataPosition() {
    this.typeofPoint();
    if (this.errorMess[0] === '') {
          const that = this;
          const id = this.model.updataId;
          const installZoneId = this.model.installZoneId;
          const regionId = this.currentArea.id;
          const name = this.model.name;
          const number = this.model.number;
          const point = this.model.point;
          const type = this.model.device.id;

          this.positionService.updataPosition(id, installZoneId, regionId, name, number, point, type).subscribe({
            next: function (val) {
              that.alerts.push({
                id: 1,
                type: 'success',
                message: '',
              });
              that.backup = that.alerts.map((alert: IAlert) => Object.assign({}, alert));
              that.mr.close();
            },
            complete: function () {
              that.getPosition(that.currentType.id, that.page, that.pagesize);
            },
            error: function (error) {
              const message = error.json().errors[0].defaultMessage;
              that.alertsModal.push({
                id: 1,
                type: 'danger',
                message: `: ${message}`,
              });
              console.log(error);
            }
          });
    } else {
      this.alertsModal.push({
        id: 1,
        type: 'danger',
        message: '',
      });
    }

  }

  // 
  openDelPosi(content, item, i) {
    const that = this;
    this.model.itemDelId = item.id;
    const modal = this.modalService.open(content, { size: 'sm' });
    this.mr = modal;

  }

  // 
  closePosition($event) {
    console.log($event);
    if ($event === 'ok') {
      this.delPosition();
    }
    this.mr.close();
  }

  // -
  delPosition() {
    const that = this;
    const id = this.model.itemDelId;
    let flag = false;
    const pages = (this.total + this.pagesize - 1) / this.pagesize;
    if (this.page >= pages && this.positionListItems.length === 1) {
      flag = true;
    }
    this.positionService.delPosition(id).subscribe({
      next: function (val) {
        that.alerts.push({
          id: 1,
          type: 'success',
          message: '',
        });
        that.backup = that.alerts.map((alert: IAlert) => Object.assign({}, alert));
      },
      complete: function () {
        if (flag) {
          that.page  = that.page - 1;
          that.getPosition(that.currentType.id, that.page, that.pagesize);
        } else {
          that.getPosition(that.currentType.id, that.page, that.pagesize);
        }
      },
      error: function (error) {
        const message = error.json().errors[0].defaultMessage;
        that.alerts.push({
          id: 1,
          type: 'danger',
          message: `: ${message}`,
        });
        console.log(error);
      }
    });
  }

  // 
  getPosition(type: number, page: number, pagesize: number) {
    const that = this;
    this.positionService.getPosition(this.queryStr, type, page, pagesize).subscribe({
      next: function (val) {
        that.positionList = val;
        that.total = val.total;
        that.positionListItems = val.items;

      },
      complete: function () {
      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // 
  pageChange() {
    this.getPosition(this.currentType.id, this.page, this.pagesize);
  }

  // 
  getDevice() {
    const that = this;
    this.positionService.getDevice().subscribe({
      next: function (val) {
        that.deviceList = val;
        that.model.device = val[0];
        that.deviceTypes = val.map((item) => Object.assign({}, item));
        that.deviceTypes.unshift({ id: 0, name: '' }); // 
        that.currentType = that.deviceTypes[0];
      },
      complete: function () {

      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // 
  deviceChange() {
    console.log(this.model.device);
  }

  // 
  getCity() {
    const that = this;

    this.positionService.getZoneDefault().subscribe({
      next: function (val) {
        that.cityList = val.regions;
        that.zoom = that.switchZone(val.zone.level);
        that.currentCity = that.getNode(that.cityList, val.zone.region_id); // 
        that.currentChildren = that.currentCity.children; // 
        that.model.installZoneId = that.currentCity.installZoneId; // 
        that.currentArea = that.currentChildren[0].children[0]; // 

      },
      complete: function () {
        //  that.addBaiduMap(); // 

      },
      error: function (error) {
        console.log(error);
      }
    });
  }


  openAddPositions(content) {
    const that = this;

    this.modalService.open(content, { size: 'lg' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;

      console.log(this.closeResult);

    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(this.closeResult);
    });

  }


  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
  // 
  addBaiduMap() {
    const map = this.map = new BMap.Map('position_map', {
      enableMapClick: true,
      // minZoom: 11
    }); // 
    const point = new BMap.Point(113.922329, 22.49656); //  --
    map.centerAndZoom(point, 19); // 
    map.enableScrollWheelZoom(true); // 
    map.setMapStyle({ style: 'normal' });
    this.mapClickOff(map);
  }

  // -
  mapClickOff(baiduMap) {
    const that = this;
    baiduMap.addEventListener('click', function (e) {
      that.model.point = e.point;
      // console.log(typeof(that.model.point.lat));

    });
  }

  typeofPoint() {
    const that = this;
    this.errorMess = [];
    const str1 = '';
    const str2 = '';
    this.validPoint(this.model.point.lat, str1);
    this.validPoint(this.model.point.lng, str2);
    if (this.errorMess.length <= 0) {
      that.errorMess.push('');
    }
  }
  // 
  validPoint(lat, str) {
    let maxValue;
    if (str === '') {
      maxValue = 90;
    } else {
      maxValue = 180;
    }
    const that = this;
    let errorMes = ''; // 
    const latNum = Number(lat);
    const latStr = latNum.toString();

    if (lat && latStr === 'NaN') {
      errorMes = str + '';
      // console.log(errorMes);
    } else if (lat && latStr !== 'NaN') {
        const pointArray = latStr.split('.');
        if (Number(pointArray[0]) >= -maxValue && Number(pointArray[0]) <= maxValue) {
            if (pointArray[1].length > 6) {
                errorMes = str + '6';
                const term = pointArray[1].slice(0, 6);
                pointArray[1] = term;
            }
            if (str === '') {
              that.model.point.lat = pointArray[0] + '.' + pointArray[1];
            } else {
              that.model.point.lng = pointArray[0] + '.' + pointArray[1];
            }
        } else {
            errorMes = str + '';
        }

    }
    errorMes === '' ? errorMes = '' : this.errorMess.push(errorMes);

  }


  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 19;
        break;
      default:
        break;
    }
    return zone;
  }
  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }
      const obj = json[i];
      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;
        break;
      } else {
        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;
          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }
    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }
    // 6.obj
    // return {
    //   parentNode: that.parentNode,
    //   node: that.node
    // };
    return that.node;
  }
  getPoint(baiduMap, city) {
    const that = this;
    // 
    const myGeo = new BMap.Geocoder();
    const zoom = this.zoom = this.switchZone(city.level);
    const fullName = city.full_name;

    const pt = city.center;
    const point = new BMap.Point(pt.lng, pt.lat);
    baiduMap.centerAndZoom(point, zoom);
  }
  // 
  // 
  selecteCity(city, i) {
    this.model.installZoneId = city.installZoneId; // 
    this.model.point = { lng: '', lat: '' };
    this.currentCity = city;
    this.getPoint(this.map, city);  // - 
    this.currentChildren = city.children;
    this.currentArea = null;
  }

  selecteblock(block) {
    this.currentArea = block;
    this.model.point = { lng: '', lat: '' };
    this.getPoint(this.map, block);  // - 
  }

  // 
  showArea() {
    this.areashow = true;
  }
  // 
  showCiyt() {
    this.cityshow = true;
  }
  // 
  arealistMouseover(area) {

    this.currentBlockList = area.children;
  }
  // 
  arealistMouseleave() {
    this.areashow = false;
    this.currentBlockList = null;
  }
  // 
  citylistMouseleave() {
    this.cityshow = false;
  }
  arealistMouseNone() {
    this.areashow = true;
    this.currentBlockList = null;
  }

}

export interface IAlert {
  id: number;
  type: string;
  message: string;
}
