import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { MAPLIST } from '../../data/map-list';
import { MonitorService } from '../../service/monitor.service';
// baidu map
declare let BMap;
declare let BMAP_ANCHOR_TOP_LEFT;


@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit {

  @ViewChild('map5') map_container: ElementRef;
  /*
  model:object
  light_list: array // 
  */
  model: any = {}; // 

  /*
  map_model: object // 
  @currentCity: any // 
  @currentArea: any // 
  @cityList: array // 
  @currentChildren: array // 
  @currentBlock: array //  = []; // 2
  */
  map_model: any = {}; // 
  map: any; // 


  device: any; // // 

  deviceChild: any; // // -
  areashow = false; // 
  cityshow = false; // 
  deviceshow = false; // 

  visible = true; // 

  parentNode = null; // JSON 
  node = null; // JSON 

  light_list = MAPLIST.val.light_list; // 

  constructor(private monitorService: MonitorService, ) {
    this.map_model.deviceList = []; // 
    this.map_model.cityList = []; // 
    this.map_model.currentChildren = []; // 
    this.map_model.currentBlock = []; // //  = []; // 2
   }

  ngOnInit() {
    this.addBeiduMap();
    this.getCity(); // 
    this.getDevice(); // 
  }

  // API
  addBeiduMap() {

    const map = this.map = new BMap.Map(this.map_container.nativeElement, {
      enableMapClick: true,
      // minZoom: 11,
      // maxZoom : 11
    }); // 


    // BMapPointPoint116.40439.915

    const point = new BMap.Point(113.922329, 22.49656); //  --
    map.centerAndZoom(point, 19); // 

    map.enableScrollWheelZoom(true); // 

    map.setMapStyle({ style: 'dark' });

    // 

    const offset = new BMap.Size(20, 55);
    const navigationControl = new BMap.NavigationControl({
      anchor: BMAP_ANCHOR_TOP_LEFT,
      offset: offset,
    });
    map.addControl(navigationControl);

    this.addMarker();

  }

  addMarker() {
    for (let index = 0; index < this.light_list.length; index++) {
      const item = this.light_list[index];
      const point = new BMap.Point(item.lng, item.lat);

      let myIcon;
      if (item.is_exception && item.is_exception === 1) { // 
        myIcon = new BMap.Icon('../../../../assets/imgs/light-breakdown.png', new BMap.Size(300, 157));
        // console.log('');
      } else if (item.is_online === 0) { // 
        myIcon = new BMap.Icon('../../../../assets/imgs/light-up-1.png', new BMap.Size(300, 157));
        // console.log('');
      } else { // 
        myIcon = new BMap.Icon('../../../../assets/imgs/light-normal.png', new BMap.Size(300, 157));
        // console.log('');

      }
      myIcon.setAnchor(new BMap.Size(16, 38));
      const marker2 = new BMap.Marker(point, { icon: myIcon });  // 
      this.map.addOverlay(marker2);
    }
  }

  // - 
  getPoint(baiduMap, city) {
    const zoom = this.switchZone(city.level);
    console.log(city);
    const pt = city.center;
    const point = new BMap.Point(pt.lng, pt.lat);
    baiduMap.centerAndZoom(point, zoom);
  }


  // 

  //  --ok
  getCity() {
    const that = this;

    this.monitorService.getZoneDefault().subscribe({
      next: function (val) {
        that.map_model.cityList = val.regions;
        // that.zoom = that.switchZone(val.zone.level);
        that.node = that.getNode(val.regions, val.zone.region_id);
        that.map_model.currentCity = that.node;
        that.map_model.currentChildren = that.node.children;
      },
      complete: function () {
      },
      error: function (error) {
        console.log(error);
      }
    });
  }
  //  -- ok
  getDevice() {
    const that = this;

    this.monitorService.getDevice().subscribe({
      next: function (val) {
        that.map_model.deviceList = val;

      },
      complete: function () {


      },
      error: function (error) {
        console.log(error);
      }
    });
  }

  // -
  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 19;
        break;
      default:
        break;
    }
    return zone;
  }

  //
  /*
   * JSON 
   */


  /**
   * NodeID
   *
   * @param  {[type]}
   * @param  {[type]}
   * @return {[type]}
   */

  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }

      const obj = json[i];

      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;

        break;
      } else {

        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;

          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }


    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }

    // 6.obj
    // return {
    //   parentNode: that.parentNode,
    //   node: that.node
    // };
    return that.node;
  }


  // 
  enterFullScreen() {
    console.log('');
    console.log(this.visible);
    // this.visible = false;
    // localStorage.setItem('visible', 'false');

    // 
    // const offset = new BMap.Size(20, 15);
    // this.navigationControl.setOffset(offset);

    // this.communicateService.sendMessage(this.visible); // 
    // this.fullScreenService.enterFullScreen();

  }


  // 
  // 
  selecteCity(city) {
    this.map_model.currentCity = city;
    this.getPoint(this.map, city);  // - 
    this.map_model.currentChildren = city.children;
  }

  selecteblock(block) {
    this.getPoint(this.map, block);  // - 
    this.map_model.currentArea = block;
  }

  // 
  showArea() {
    this.areashow = true;
  }
  // 
  showCiyt() {
    this.cityshow = true;
  }
  // 
  showDevice() {
    this.deviceshow = true;
  }

  // 
  arealistMouseover(area) {

    this.map_model.currentBlock = area.children;
  }
  // 
  arealistMouseleave() {
    this.areashow = false;
    this.map_model.currentBlock = [];
  }
  // 
  citylistMouseleave() {
    this.cityshow = false;
  }
  // 
  devicelistMouseleave() {
    this.deviceshow = false;
  }
  arealistMouseNone() {
    this.areashow = true;
    this.map_model.currentBlock = [];
  }
}

