  // -
  switchZone(level) {
    let zone = 12;
    switch (level) {
      case 1:
        zone = 10;
        break;
      case 2:
        zone = 12;
        break;
      case 3:
        zone = 15;
        break;
      case 4:
        zone = 19;
        break;
      default:
        break;
    }
    return zone;
  }

  //
  /*
   * JSON 
   */


  /**
   * NodeID
   *
   * @param  {[type]}
   * @param  {[type]}
   * @return {[type]}
   */

  getNode(json, nodeId) {
    const that = this;

    // 1. root JSON
    for (let i = 0; i < json.length; i++) {
      if (that.node) {
        break;
      }

      const obj = json[i];

      // 
      if (!obj || !obj.id) {
        continue;
      }
      // console.log(nodeId);
      // console.log(obj.id);
      // 2.
      if (obj.id === nodeId) {
        // nodeId
        that.node = obj;

        break;
      } else {

        // 3.
        if (obj.children) {
          // 4.parent 
          that.parentNode = obj;

          // 
          that.getNode(obj.children, nodeId);
        } else {
          // 
          continue;
        }
      }
    }


    // 5.null
    if (!that.node) {
      that.parentNode = null;
    }

    // 6.obj
    // return {
    //   parentNode: that.parentNode,
    //   node: that.node
    // };
    return that.node;
  }


